@media screen and (max-width: 768px) {

    .paginaCadastroContainer {
        display: flex;
        flex-direction: column-reverse;
        /*align-items: center;*/
        margin-left: 5%;
        margin-top: 10%;
        height: 800px;
    }

    .paginaCadastroLadoEsquerdo {
        background-image: url('../../images/cadastrofundomobile.svg');
        height: 50%;
        background-repeat: no-repeat;
        background-position: bottom left;
        margin-left: -5%;
    }

    .paginaCadastroLadoDireito {
        width: 90%;
    }

    .paginaCadastroTextoTitulo {
        font-family: Montserrat;
        font-style: normal;
        font-weight: bold;
        font-size: 22px;
        line-height: 33px;
        color: #212529;
    }

    .paginaCadastroFormulario {
        height: 40%;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
    }

    .paginaCadastroTextoSubtitulo {
        font-family: Open Sans;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 24px;
        display: flex;
        align-items: center;
        color: #343A40;
    }

    .paginaCadastroFormularioInput {
        width: 100%;
        border-bottom: 1px solid #ADB5BD;
        border-width: 0 0 2px;
        font-family: Open Sans;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 24px;
        display: flex;
        align-items: center;
        color: #ADB5BD;
        padding: 5px;
    }

    .paginaCadastroFormularioTermo {
        display: flex;
        margin: 5% 0 5% 0;
    }

    .paginaCadastroFormularioTermoString {
        font-family: Open Sans;
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        line-height: 18px;
        display: flex;
        align-items: center;
        color: #868E96;
    }

    .paginaCadastroFormularioBotao {
        width: 100%;
        height: 40px;
        left: 35px;
        top: 575px;
        background: #71CAD4;
        border-radius: 4px;
        justify-content: center;
        font-family: Open Sans;
        font-style: normal;
        font-weight: 600;
        font-size: 16px;
        line-height: 24px;
        display: flex;
        align-items: center;
        text-align: center;
        color: #FFFFFF;
        transition: ease 0.4s;
    }
    
    .hideOrSeePasswordTag {
        position: absolute;
        left: 85%;
        z-index: 1;
        cursor: pointer;
    }

    .hideOrSeePasswordConfirmationTag {
        position: absolute;
        left: 85%;
        z-index: 1;
        cursor: pointer;
    }

    .checkboxTag {
        margin-right: 5px;
    }

    .mensagemAceitarTermos {
        font-family: Open Sans;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 24px;
        display: flex;
        align-items: center;
        color: #343A40;
    }

    .mensagemAceitarTermosMensagem {
        margin-left: 5px;
    }

    .mensagemSenha {
      font-family: Open Sans;
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 14px;
      display: flex;
      align-items: center;
      color: #868E96;
      margin-bottom: 1%;
    }
    
    .mensagemSenhaMensagem {
      margin-left: 5px;    
    }

    .paginaCadastroTipoPessoaContainer {
        display: flex;
        margin: 5% 0 5% 0;
    }
    
    .paginaCadastroTipoPessoa {
        margin-right: 5%;
        cursor: pointer;
        font-family: Open Sans;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 24px;    
        color: #343A40;
        display: flex;
    }
    
    .paginaCadastroTipoPessoaIcone {
        margin-right: 5px;
    }

    .cadastroConfirmacaoContainer {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 600px;
        background-image: url('../../images/montanhasfundo.svg');
        background-repeat: no-repeat;
        background-position: center bottom;
    }
    
    .cadastroConfirmacaoConteudo {
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
    }
    
    .cadastroConfirmacaoTitulo {
        font-family: Montserrat;
        font-style: normal;
        font-weight: bold;
        font-size: 22px;
        line-height: 33px;
        color: #212529;
    }
    
    .cadastroConfirmacaoSubtitulo {
        font-family: Open Sans;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 24px;
        display: flex;
        align-items: center;
        color: #343A40;
    }
    

}

@media screen and (min-width: 769px) {

    .paginaCadastroContainer {
        height: 700px;
        display: flex;
        background-color: #fff;
    }

    .paginaCadastroLadoEsquerdo {
        background-image: url('../../images/cadastro.svg');
        width: 50%;
        background-repeat: no-repeat;
        background-position: bottom left;
    }

    .paginaCadastroLadoDireito {
        width: 50%;
    }

    .paginaCadastroLadoDireito {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
    }

    .paginaCadastroTexto {
        width: 55%;
    }

    .paginaCadastroTextoTitulo {
