        left: 75%;
        z-index: 1;
        cursor: pointer;
        margin-top: 10px;
    }

    .hideOrSeePasswordConfirmationTag {
      position: absolute;
      left: 75%;
      z-index: 1;
      cursor: pointer;
      margin-top: 10px;
  }

  .mensagemAceitarTermos {
